import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Link } from "common/components/Link";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { Icon } from "common/components/Icons";
import { SocialMediaIcon } from "common/components/Icons/SocialMediaIcon";

import { SocialMediaProps, SocialMediaLink } from "./SocialMedia.types";
import styles from "./SocialMedia.module.scss";

const cx = classNames.bind(styles);

export const SocialMedia: FunctionComponent<SocialMediaProps> = ({
    list = [],
    title,
    className,
}) => {
    return (
        <div className={cx("root", className)}>
            {title && (
                <Heading
                    tag={HeadingTag.H2}
                    type={HeadingType.M}
                    className={cx("title")}
                >
                    {title}
                </Heading>
            )}
            <ul className={cx("list")}>
                {list.map((item: SocialMediaLink) => {
                    const { name, icon, link } = item;
                    return (
                        <li className={cx("list__item")} key={link}>
                            <Link
                                id={getAnalyticsId({
                                    zone: "social-media",
                                    element: GetAnalyticsIdElement.LINK,
                                    label: name?.toLowerCase(),
                                })}
                                link={link}
                                className={cx("link")}
                            >
                                {name}
                                <Icon className={cx("icon")}>
                                    <SocialMediaIcon iconName={icon} />
                                </Icon>
                            </Link>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
};
