import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Link } from "common/components/Link";
import { Icon } from "common/components/Icons";
import { SocialMediaIcon } from "common/components/Icons/SocialMediaIcon";

import { ContactItemLandingType } from "content/common/content.types";

import { FooterProps } from "./FooterPromo.types";
import styles from "./FooterPromo.module.scss";

const cx = classNames.bind(styles);

export const FooterPromo: FunctionComponent<FooterProps> = ({
    title,
    list = [],
}) => {
    return (
        <div className={cx("root")}>
            <Heading
                tag={HeadingTag.H3}
                type={HeadingType.S}
                className={cx("title")}
            >
                {title}
            </Heading>

            <ul className={cx("list")}>
                {list.map((item: ContactItemLandingType) => {
                    const { name, icon, link } = item;

                    return (
                        <li key={link}>
                            <Link
                                id={getAnalyticsId({
                                    zone: "footer",
                                    element: GetAnalyticsIdElement.LINK,
                                })}
                                className={cx("link", icon)}
                                link={link}
                            >
                                <Icon className={cx("icon")}>
                                    <SocialMediaIcon iconName={icon} />
                                </Icon>
                                {name}
                            </Link>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
};
