import { FunctionComponent, useState, useEffect } from "react";
import classNames from "classnames/bind";

import { BgSliderProps } from "./BgSlider.types";
import styles from "./BgSlider.module.scss";

const cx = classNames.bind(styles);

const DELAY = 4000;

export const BgSlider: FunctionComponent<BgSliderProps> = ({ list = [] }) => {
    const [activeItem, setActiveItem] = useState(0);

    useEffect(() => {
        const timer = setInterval(() => {
            if (activeItem + 1 < list.length) {
                setActiveItem(activeItem + 1);
            } else {
                setActiveItem(0);
            }
        }, DELAY);

        return () => clearInterval(timer);
    }, [activeItem]);

    return (
        <div className={cx("root")}>
            {list.map((background: string, index: number) => {
                const active = index === activeItem;

                return (
                    <div
                        style={{
                            backgroundImage: `url(${background})`,
                        }}
                        className={cx("poster", active && "poster_active")}
                        key={background}
                    />
                );
            })}
        </div>
    );
};
