import { FC } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { SocialMedia } from "common/components/SocialMedia";
import { Link } from "common/components/Link";
import { getAnalyticsId } from "common/utils/AnalyticsId";
import { BookAppointmentFullForm } from "common/components/BookAppointmentFullForm";

import { BgSlider } from "components/BgSlider";
import { FooterPromo } from "components/Footer/FooterPromo";
import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";

import { GiveawayLayoutProps } from "./GiveawayLayout.types";

import styles from "./GiveawayLayout.module.scss";

const cx = classNames.bind(styles);

export const GiveawayLayout: FC<GiveawayLayoutProps> = ({
    content,
    language,
    version,
}) => {
    const {
        title,
        footer,
        subtitle,
        contacts,
        background,
        socialMedia,
    } = content;
    const contactsList = [contacts?.phone, contacts?.wa, contacts?.email];

    return (
        <div className={cx("root")}>
            <div className={cx("column", "column_content")}>
                <div className={cx("content")}>
                    <Link className={cx("logo")} link={contacts?.site?.link}>
                        <Icon>
                            <LogoIcon />
                        </Icon>
                    </Link>
                    <div className={cx("header")}>
                        <Heading tag={HeadingTag.H1} className={cx("title")}>
                            {title}
                        </Heading>
                        {subtitle && (
                            <Text className={cx("subtitle")}>{subtitle}</Text>
                        )}
                    </div>
                    <BookAppointmentFullForm
                        version={version}
                        language={language}
                        analyticId={getAnalyticsId({
                            zone: "giveaway-page",
                        })}
                        className={cx("appointment")}
                        withInstagram={true}
                    />
                    <SocialMedia
                        {...socialMedia}
                        className={cx("social-media")}
                    />
                    <FooterPromo {...footer} list={contactsList} />
                </div>
            </div>
            <div className={cx("column", "background")}>
                <BgSlider list={background} />
            </div>
        </div>
    );
};
